/* eslint-disable */
import { Scene } from 'phaser'
import buildingInfo from '@/assets/data/building-info.json'

import Planet from '@/assets/image/planet/planet-1.png'
import PlanetLight from '@/assets/image/planet/planet-1-light.png'
import Planet1 from '@/assets/image/planet/planet-2.png'
import StarRain from '@/assets/image/planet/blue.png'
import BGM from '@/assets/audio/bgm.mp3'

import tiles from '@/assets/tile-1.png'
import nckuShort from '@/assets/image/building/ncku-short.png'
import church from '@/assets/image/building/church.png'
import bs from '@/assets/image/building/bs.png'
import car from '@/assets/image/movingObj/car40.png'
import buildProcess1x1 from '@/assets/image/building/build-process-1x1.png'
import buildCompleted1x1 from '@/assets/image/building/build-completed-1x1.png'
import bg from '@/assets/image/background/bg.png'
import windmill from '@/assets/image/building/windmill-spawn.png'
import windmilling from '@/assets/image/building/windmill-work.png'
import powerPlant from '@/assets/image/building/power-plant-spawn.png'
import powerPlanting from '@/assets/image/building/power-plant-work.png'

export default class BootScene extends Scene {
  constructor() {
    super({ key: 'BootScene' })
  }

  preload() {
    // in planetScene
    this.load.audio('bgm', BGM)
    this.load.image('Star', StarRain)
    this.load.image('bg', bg)
    this.load.image('planet-1', Planet)
    this.load.image('planet-2', Planet1)
    this.load.image('planet-1-light', PlanetLight)

    // in playScene
    this.load.image('tiles', tiles)
    this.load.image('ncku', nckuShort)
    this.load.image('church', church)
    this.load.image('bs', bs)
    this.load.image('bg', bg)
    this.load.spritesheet('windmilling', windmilling, {frameWidth: 192,frameHeight: 222.5})
    this.load.spritesheet('windmill', windmill, {frameWidth: 192,frameHeight: 220})
    this.load.spritesheet('powerPlanting', powerPlanting, {frameWidth: 192,frameHeight: 220, startFrame: 0, endFrame: 65})
    this.load.spritesheet('powerPlant', powerPlant, {frameWidth: 192,frameHeight: 220, startFrame: 0, endFrame: 22})
    this.load.spritesheet('car', car, {frameWidth: 72,frameHeight: 72})
    this.load.spritesheet('buildProcess1x1ing', buildProcess1x1, {frameWidth: 192, frameHeight: 300})
    this.load.spritesheet('buildCompleted1x1ing', buildCompleted1x1, {frameWidth: 192, frameHeight: 300})
    this.load.json('buildingInfo', buildingInfo)
  }
  create() {
    this.scene.start('PlanetScene')
  }
}
