import Phaser from 'phaser'
import BootScene from './scences/BootScene'
import PlayScene from './scences/PlayScene'
import PlanetScene from './scences/PlanetScene'

function launch () {
  var newGame = new Phaser.Game({
    type: Phaser.AUTO,
    width: document.body.clientWidth,
    height: document.body.clientHeight,
    backgroundColor: '#000000',
    parent: 'game-container',
    scene: [BootScene, PlanetScene, PlayScene]
  })
  console.log(newGame)
}

export { launch }
